* {
  box-sizing: border-box;
}

h1, h2, h3, h4, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  margin-left: 0;
  padding-left: 0;
}

li {
  list-style: none;
}

input {
  outline: none;
}

button {
  border: none;
}

.txt {
  color: #2e3034;
  font-size: 20px;
}

body {
  background-color: #a2b6b5;
}

.text {
  color: #2e3034;
}

.link {
  color: #6f7239;
  width: 125px;
  background-color: rgba(223, 226, 124, .81);
  border: 3px solid #6f7239;
  padding: 10px;
  text-decoration: none;
}

.link:hover, .button:hover {
  color: rgba(223, 226, 124, .81);
  background-color: #6f7239;
  border-color: rgba(223, 226, 124, .81);
}

.lab {
  flex-direction: column;
  gap: 15px;
  margin-top: 25px;
  display: flex;
}

.return {
  color: #6f7239;
  margin-bottom: 15px;
  font-size: 18px;
  text-decoration: none;
  display: block;
}

.form {
  width: 350px;
  color: #2e3034;
  border: 3px solid #6f7239;
  flex-direction: column;
  gap: 15px;
  padding: 10px;
  font-size: 18px;
  display: flex;
}

.select {
  height: 30px;
  width: 100%;
  box-sizing: border-box;
  border: 3px solid #6f7239;
  font-size: 16px;
}

.input {
  width: 100%;
  height: 25px;
  box-sizing: border-box;
  border: 3px solid #6f7239;
  font-size: 14px;
}

.button {
  color: #6f7239;
  cursor: pointer;
  background-color: rgba(223, 226, 124, .81);
  border: 3px solid #6f7239;
  margin: auto;
  padding: 10px;
}

.wrapper {
  flex-direction: column;
  gap: 10px;
  display: flex;
}

